// import { positionVert, colorFrag } from './shaders';
import { Solution } from '..';
import * as triangle from '../Geometry/triangle';

interface SIZE{
    width:number;
    height:number;
}
export interface pipeline {
    pipeline: GPURenderPipeline,
    vertexBuffer: GPUBuffer,
    colorBuffer: GPUBuffer,
    uniformGroup: GPUBindGroup
}

export class Renderer {
    solution: Solution;
    canvas: HTMLCanvasElement;
    ctx: GPUCanvasContext;

    device: GPUDevice;
    format: GPUTextureFormat;

    size: SIZE;

    // pipelineObj: pipeline;
    commandEncoder: GPUCommandEncoder;
    view: GPUTextureView
    renderPassDescriptor: GPURenderPassDescriptor;
    passEncoder: GPURenderPassEncoder;

        uniformBuffer: GPUBuffer

    requestAni;

    constructor(solution: Solution) {
        this.solution = solution;
        this.canvas = solution.canvas;
    }
    async initWebGPU() {
        if (!navigator.gpu) { throw new Error('Not Support WebGPU'); }

        const adapter = await navigator.gpu.requestAdapter({
            powerPreference: 'high-performance'
            // powerPreference: 'low-power'
        })

        if (!adapter) { throw new Error('No Adapter Found'); }

        const device = await adapter.requestDevice()
        this.ctx = this.canvas.getContext('webgpu') as GPUCanvasContext
        // @ts-ignore
        const format = navigator.gpu.getPreferredCanvasFormat ? navigator.gpu.getPreferredCanvasFormat() : this.ctx.getPreferredFormat(adapter)
        // const format = 'bgra8unorm';
        const devicePixelRatio = window.devicePixelRatio || 1
        this.canvas.width = this.canvas.clientWidth ;
        this.canvas.height = this.canvas.clientHeight;
        this.ctx.configure({
            // json specific format when key and value are the same
            device, format,
            // prevent chrome warning
            alphaMode: 'opaque'
        })

        this.size = { width: this.canvas.width, height: this.canvas.height }
        this.device = device;
        this.format = format;

        // await this.initPipeline(this.device, this.format);
        // this.start();
        this.loop();
    }
    public loop = () => {
        // this.start();
        this.solution.scene.render();
        // this.draw(this.device, this.ctx, this.pipelineObj);
        this.requestAni = requestAnimationFrame(this.loop);
        
    }
    public draw (pipelineObj: pipeline) {
        if (pipelineObj === undefined) { return;}
        // const rotationAngle = Date.now() / 1000;
        // console.log('run', rotationAngle);

        this.solution.camera.update();

        // this.device.queue.writeBuffer(this.uniformBuffer, 0, this.solution.camera.mvpMatrix as ArrayBuffer);

        this.commandEncoder = this.device.createCommandEncoder();
        this.view = this.ctx.getCurrentTexture().createView();
        this.renderPassDescriptor = {
            //@ts-ignore
            colorAttachments: [{
                    view: this.view,
                    clearValue: { r: 0, g: 0, b: 0, a: 1.0 },
                    loadOp: 'clear',
                    storeOp: 'store'
                }]
        };

        // console.log(pipelineObj);
        this.passEncoder = this.commandEncoder.beginRenderPass(this.renderPassDescriptor);
        this.passEncoder.setPipeline(pipelineObj.pipeline);
        this.passEncoder.setBindGroup(0, pipelineObj.uniformGroup); // set uniformGroup
        this.passEncoder.setVertexBuffer(0, pipelineObj.vertexBuffer); // set vertex
        this.passEncoder.draw(triangle.vertexCount); // 3 vertex form a triangle
        this.passEncoder.end();
        // webgpu run in a separate process, all the commands will be executed after submit
        this.device.queue.submit([this.commandEncoder.finish()]);   
    }
    public destroy() {
        this.device.destroy();
        // this.isAnimation = false;
        cancelAnimationFrame(this.requestAni);
        // this.requestAni = undefined;
    }
}