import { Solution } from '..';
export class EventListener {
    solution: Solution;
    constructor(solution: Solution) {
        this.solution = solution;
        this.appendEvent();
    }
    public appendEvent() {
        this.solution.divHost.addEventListener('mousedown', (e: MouseEvent) => {
            console.log(e);
            this.solution.controller.addTri();
        });
    }
}