import { Solution } from '..';
import { Geometry } from '../Geometry/Base/Geometry';

import { Tri } from '../Geometry/Tri';

export class Scene {
    public solution: Solution;
    public geometries: Geometry[] = [];

    private index = 0;
    constructor(solution: Solution) {
        this.solution = solution;




    }
    public init() {
        this.geometries.push(new Tri(this));
    }
    public render() {
        //

        for(this.index = 0; this.index < this.geometries.length; ++this.index) {
            this.solution.renderer.draw(this.geometries[this.index].pipeline);
        }

        console.log('rendering...', this.geometries.length);
    }
}