import { vec3 } from 'gl-matrix';
import { Scene } from '../Scene';
import { positionVert, colorFrag } from '../Renderer/shaders';
import { Geometry } from './Base/Geometry';
export class Tri extends Geometry {
    scene: Scene;
    vertex = new Float32Array([
        0.0, 0.1, 0.0,
        -0.5, -0.5, 0.0,
        0.5, -0.5, 0.0
    ]);
    vertexCount: number = 3;

    position = vec3.fromValues(0,0,0);

    constructor(scene: Scene) {
        super();
        this.scene = scene;
        this.position[0] = 0.5;

        const num = Math.random();
        this.vertex[1]+= num;

        this.initPipeline(this.scene.solution.renderer.device, this.scene.solution.renderer.format);

    }
    async initPipeline(device: GPUDevice, format: GPUTextureFormat) {
        const pipeline = await device.createRenderPipelineAsync({
            label: 'Basic Pipline',
            layout: 'auto',
            vertex: {
                module: device.createShaderModule({
                    code: positionVert,
                }),
                entryPoint: 'main',
                buffers: [{
                    arrayStride: 3 * 4, // 3 float32,
                    attributes: [{
                            // position xyz
                            shaderLocation: 0,
                            offset: 0,
                            format: 'float32x3',
                        }]
                }]
            },
            fragment: {
                module: device.createShaderModule({
                    code: colorFrag,
                }),
                entryPoint: 'main',
                targets: [{
                        format: format
                    }]
            },
            primitive: {
                topology: 'triangle-list' // try point-list, line-list, line-strip, triangle-strip?
            }
        } as GPURenderPipelineDescriptor)
        // create vertex buffer
        const vertexBuffer = device.createBuffer({
            label: 'GPUBuffer store vertex',
            size: this.vertex.byteLength,
            usage: GPUBufferUsage.VERTEX | GPUBufferUsage.COPY_DST,
            //mappedAtCreation: true
        })
        device.queue.writeBuffer(vertexBuffer, 0, this.vertex)
        // create color buffer
        const colorBuffer = device.createBuffer({
            label: 'GPUBuffer store rgba color',
            size: 4 * 4, // 4 * float32
            usage: GPUBufferUsage.UNIFORM | GPUBufferUsage.COPY_DST,
        })
        device.queue.writeBuffer(colorBuffer, 0, new Float32Array([1, 0, 0, 1]))

        // create a uniform group for color
        const uniformGroup = device.createBindGroup({
            label: 'Uniform Group with colorBuffer',
            layout: pipeline.getBindGroupLayout(0),
            entries: [
                {
                    binding: 0,
                    resource: {
                        buffer: colorBuffer
                    }
                }
            ]
        })
        this.pipeline = { pipeline, vertexBuffer, colorBuffer, uniformGroup }
        this.scene.geometries.push(this);
    }
}