import { Renderer } from './Renderer';
import { Scene } from './Scene';
import { EventListener } from './EventListener';
import { Controller } from './Controller';
import { UI } from './UI';

import { Camera } from './Camera';

import { Tri } from './Geometry/Tri';

// https://github.com/jack1232/WebGPU-Step-By-Step

export class Solution {
    public divHost: HTMLElement;
    public canvas: HTMLCanvasElement;

    public scene: Scene;
    public renderer: Renderer;
    public camera: Camera;
    public ui: UI;
    public eventListener: EventListener;
    public controller: Controller;

    constructor(id: string) {
        this.divHost = document.getElementById(id);
        this.divHost.style.position = 'relative';

        this.canvas = document.createElement('canvas');
        this.divHost.appendChild(this.canvas);
        this.canvas.width = this.divHost.clientWidth;
        this.canvas.height = this.divHost.clientHeight;
        this.canvas.style.background = 'rgba(0,0,0,0)';
        // this.canvas.style.position = 'absolute';

        this.init();
    }
    public init = async () => {
        this.camera = new Camera(this);
        this.scene = new Scene(this);
        this.ui = new UI(this);
        this.eventListener = new EventListener(this);
        this.controller = new Controller(this);
        this.renderer = new Renderer(this);
        await this.initRenderer();
        this.scene.init();
        // this.renderer.render();


        this.initPost();
    }
    public initPost() {
        
    }
    private initRenderer = async () => {
        return this.renderer.initWebGPU();
    }
    public destroy() {
        this.renderer.destroy();

        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}

