import * as triangle from '../Geometry/triangle';
import { getSlider } from '../../Util';
import { Solution } from '..';

export class UI {
    public divHost: HTMLElement;
    public solution: Solution;
    public divColor: HTMLInputElement;
    public divRange: any;

    constructor(solution: Solution) {
        this.solution = solution;
        this.divHost = solution.divHost;

        this.divColor = document.createElement('input');
        this.divColor.type = 'color';
        this.divColor.defaultValue = '#ff0000';
        // this.divColor.style.position = 'absolute';
        this.divHost.appendChild(this.divColor);

        this.divRange = getSlider('size', 0, 100);
        this.divHost.appendChild(this.divRange.div);

        this.appendEvent();
    }
    public appendEvent() {
        // update colorBuffer if color changed
        this.divColor.addEventListener('input', (e: Event) => {
            const color = (e.target as HTMLInputElement).value; // get hex color string
            const r = +('0x' + color.slice(1, 3)) / 255; // parse hex color into rgb
            const g = +('0x' + color.slice(3, 5)) / 255;
            const b = +('0x' + color.slice(5, 7)) / 255;
            // write colorBuffer with new color
            // this.solution.nWebGPU.device.queue.writeBuffer(this.solution.nWebGPU.pipelineObj.colorBuffer, 0, new Float32Array([r, g, b, 1]));
            // this.solution.nWebGPU.render();
        });

        // update vertexBuffer
        this.divRange.slider.addEventListener('input', (e: Event) => {
            // get input value
            const value = +(e.target as HTMLInputElement).value;
            console.log(value)
            // chagne vertex 0/3/6
            triangle.vertex[0] = 0;
            triangle.vertex[3] = -0.5 + -(value * 0.01);
            triangle.vertex[6] = 0.5 + (value * 0.01);
            // write vertexBuffer with new vertex
            // this.solution.nWebGPU.device.queue.writeBuffer(this.solution.nWebGPU.pipelineObj.vertexBuffer, 0, triangle.vertex);
            // this.solution.nWebGPU.render();
        });

        // re-configure context on resize
        window.addEventListener('resize', () => {
            this.solution.canvas.width = this.solution.divHost.clientWidth;
            this.solution.canvas.height = this.solution.divHost.clientHeight;
            // don't need to recall context.configure() after v104
            // this.solution.nWebGPU.render();
        });
    }
}